import { Carousel, WingBlank } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import React from 'react'
class Lunbo extends React.Component {
  state = {
    data: ['./img/lb1.jpg','./img/lb2.jpg','./img/lb3.jpg','./img/lb4.jpg','./img/lb5.jpg'],
    imgHeight: 110,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg',
        'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
        'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
        'https://gw.alicdn.com/imgextra/i1/154/O1CN01DQ9IFI1D0Zcr4wn9m_!!154-0-lubanu.jpg',
        'https://gw.alicdn.com/imgextra/i2/190/O1CN01dGopH81DH3spwMBkC_!!190-0-lubanu.jpg'
         ],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite={true}
          style={{marginTop:'40px'}}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default withRouter(Lunbo);