import React, { Component } from 'react'
import { NavLink, Route, Redirect, useHistory, useLocation, useParams, useRouteMatch, withRouter } from 'react-router-dom'
import { NavBar, Icon, Tabs, Carousel } from 'antd-mobile';
import XiangQing from './XiangQing';
import Search from './Search';
import AppTab from './AppTab';
import Lunbo from './Lunbo';
import spList from './sp.list';



const AppHome = (props) => {

    return (
        <div>
            <div style={{ height: '40px', width: '100%', backgroundColor: 'orange', position: 'fixed', zIndex: '1', top: '0' }}>
                <img style={{ height: '30px', width: '30px', textAlign: 'center', marginLeft: '10px', marginTop: '5px', float: 'left' }} src={require('./img/taobao.png')} />
                <input placeholder="寻找宝贝店铺" style={{ height: '30px', width: '80%', float: 'left', border: 'none', backgroundColor: 'rgb(255,78,34)', marginTop: '5px', marginLeft: '10px', borderRadius: '5px' }}
                    onClick={() => {
                        props.history.push('/search')
                    }}
                ></input>
            </div>

            <Lunbo style={{ marginTop: '40px' }} />

            <div style={{ height: '200px', width: '100%', backgroundColor: 'white' }}>
                <div style={{ display: 'flex', flex: 'row' }}>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s1.png')} />
                        <p style={{ textAlign: 'center' }}>天猫新品</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s2.png')} />
                        <p style={{ textAlign: 'center' }}>今日爆款</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s3.png')} />
                        <p style={{ textAlign: 'center' }}>天猫国际</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s4.png')} />
                        <p style={{ textAlign: 'center' }}>饿了么</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s5.png')} />
                        <p style={{ textAlign: 'center' }}>天猫超市</p>
                    </div>
                </div>
                <div style={{ display: 'flex', flex: 'row' }}>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s6.png')} />
                        <p style={{ textAlign: 'center' }}>充值中心</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s7.png')} />
                        <p style={{ textAlign: 'center' }}>机票酒店</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s8.png')} />
                        <p style={{ textAlign: 'center' }}>金币庄园</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s9.png')} />
                        <p style={{ textAlign: 'center' }}>阿里拍卖</p>
                    </div>
                    <div style={{ height: '100px', width: '20%' }}>
                        <img style={{ height: '60px', width: '100%' }} src={require('./img/s10.png')} />
                        <p style={{ textAlign: 'center' }}>淘宝吃货</p>
                    </div>
                </div>
            </div>
            <img style={{ height: '30px', width: '100%', marginTop: '10px' }} src={require('./img/mx.png')} />

            <div style={{ width: '100%' }}>
                {
                    spList.map((item, idx) => {
                        return <li
                            style={{
                                display: 'inline-block',
                                width: '45%',
                                height: '280px',
                                backgroundColor: 'white',
                                marginLeft: '3.3%',
                                marginTop: '20px',
                                borderRadius: '10px'
                            }}
                            onClick={() => {
                                props.history.push({ pathname: '/xiangqing',index: `${idx}`, price: `${item.price}`, name: `${item.name}`,src:`${item.src}`} );
                            }}
                        >
                            <img style={{
                                width: '100%',
                                height: '220px',
                            }} src={`${item.src}`} key={`${idx}`} />
                            <div style={{ paddingLeft: '15px',marginTop:'5px' }}>
                                {`${item.name}`}
                                <br />
                                <span style={{ color: 'red'}}>{`${item.price}`}</span>
                            </div>
                        </li>


                    })
                }
            </div>
            <AppTab />
        </div>


    )
}
export default withRouter(AppHome)
